.questions {
  position: relative;
  background-image: url("../img/watercolor-questions.png");
  background-repeat: no-repeat;
  background-size: contain;
}

.questions::before {
  content: "";
  width: 194px;
  height: 177px;
  background-image: url("../img/bag-desktop.png");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  bottom: 0;
  left: 0;

  @media (min-resolution: $retina-dppx), (min-resolution: $retina-dpi) {
    background: url("../img/bag-desktop@2x.png") no-repeat;
    background-size: auto;
  }
}

.questions__head {
  cursor: pointer;
  margin-top: 7px;
  position: relative;
  padding-left: 30px;
  padding-right: 20px;
  padding-top: 30px;
  padding-bottom: 15px;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 150%;
}

.questions__span {
  width: 400px;
  display: block;
}

.questions__item {
  width: 560px;
  border-bottom: 1px $color-border-dashed solid;
}

.questions__head::before {
  width: 50px;
  height: 50px;
  position: absolute;
  left: 0;
  top: 15px;
  font-style: normal;
  font-weight: bold;
  font-size: 28px;
  line-height: 170%;
  color: $color-hover;
}

.questions__head--passive::after {
  content: "";
  width: 18px;
  height: 20px;
  position: absolute;
  right: 34px;
  top: 35px;
  background-image: url("../img/down.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.questions__head--active::after {
  background-image: url("../img/up.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.questions__head--1::before {
  content: "1";
}

.questions__head--2::before {
  content: "2";
}

.questions__head--3::before {
  content: "3";
}

.questions__head--4::before {
  content: "4";
}

.questions__head--5::before {
  content: "5";
}

.questions__head--6::before {
  content: "6";
}

.questions__answer {
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 140%;
  opacity: 0.74;
}

.questions__wrap {
  display: flex;
  width: calc(100% - 220px);
  max-width: 1366px;
  margin: 0 auto;
  margin-top: 100px;
}

.questions__container {
  width: 560px;
}

.questions__img-wrap {
  position: relative;
  top: 30px;
  right: -50px;
}

.questions__list {
  padding-left: 0;
}

.questions__h {
  margin-bottom: 0;
}

@media (max-width: $tablet-width) {
  .questions__img-wrap {
    display: none;
  }

  .questions__container {
    margin: 0 auto;
  }
}

@media (max-width: $mobile-width) {
  .questions__h {
    width: 290px;
    text-align: center;
    margin: 0 auto;
  }

  .questions__wrap {
    width: 310px;
    margin-top: 70px;
    margin-left: auto;
    margin-right: auto;
  }

  .questions__item {
    width: 290px;
  }

  .questions__span {
    width: 230px;
    display: block;
  }

  .questions__head {
    padding-top: 20px;
  }

  .questions__head::before {
    left: 10px;
  }

  .questions__head::after {
    right: 0;
  }

  .questions__answer {
    padding-left: 16px;
  }
}
